import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
	state:{
		carPanelData:[],
		maxOff:false,
		carShow:false,
		ball:{
			show : false,
			el : null,
			img : ''
		},
		//地址
		receiveInfo : [{
		  "name": "王某某",
		  "phone": "13811111111",
		  "areaCode": "010",
		  "landLine": "64627856",
		  "provinceId": 110000,
		  "province": "北京市",
		  "cityId": 110100,
		  "city": "市辖区",
		  "countyId": 110106,
		  "county": "海淀区",
		  "add": "上地十街辉煌国际西6号楼319室",
		  "default": true
		},{
		  "name": "李某某",
		  "phone": "13811111111",
		  "areaCode": "010",
		  "landLine": "64627856",
		  "provinceId": 110000,
		  "province": "北京市",
		  "cityId": 110100,
		  "city": "市辖区",
		  "countyId": 110106,
		  "county": "海淀区",
		  "add": "上地十街辉煌国际东6号楼350室",
		  "default": false
		}],
		orderData : []
	},
	getters:{
		//数量
		totleCount(state){
			let count=0
			state.carPanelData.forEach(goods => {
				count+=goods.count
			})
			return count
		},		
		//价格
		totlePrice(state){
			let price=0
			state.carPanelData.forEach(goods => {
				price+=goods.count * goods.price
			})
			return price
		},
		allChecked(state){
			let allcheck=true
			state.carPanelData.forEach(goods => {
				if(!goods.check){
					allcheck=false
					return allcheck
				}
			})
			return allcheck
		},
		//计算选择商品总金额
		checkedPrice(state){
			let price=0
			state.carPanelData.forEach(goods => {
				if(goods.check){
					price += goods.count * goods.price
				}
			})
			return price
		},
		//计算选择商品总量
		checkedCount(state){
			let count=0
			state.carPanelData.forEach(goods => {
				if(goods.check){
					count += goods.count
				}
			})
			return count
		},
		//获取结算页面商品列表
		checkGoods(state){
			let checkGoods =[]
			state.carPanelData.forEach(goods => {
				if(goods.check){
					checkGoods.push(goods)
				}
			})
			return checkGoods
		}
	},
	mutations:{
		addCarpanelData(state,data){
			let bOFF = true
			
			state.carPanelData.forEach(goods => {
				if(goods.sku_id === data.info.sku_id){
					if(goods.count + data.count <= goods.limit_num){
						goods.count += data.count
					}else{
						state.maxOff=true
					}
					bOFF = false
				}
			})
			
			if(bOFF){
				let goodsData = data.info
				Vue.set(goodsData,'count',data.count)
				Vue.set(goodsData,'check',true)
				state.carPanelData.push(goodsData)
			}
			state.ball.show=true
			state.ball.img = data.info.ali_image
			state.ball.el = (window.event.path?window.event.path:(data.event.composedPath && data.event.composedPath()))[0]
			state.carShow=true
		},
		eventtext(e){
		},
		closePrompt(state){
			state.maxOff=false
		},
		//删除购物车商品
		delCarpanelData(state,id){
			state.carPanelData.forEach((goods,index) =>{
				if(goods.sku_id === id){
					state.carPanelData.splice(index,1)
					return
				}
			})
		},
		showCar(state){
			state.carShow=true
		},
		hideCar(state){
			setTimeout(() => {
				state.carShow=false
			},1000)
		},
		//增加
		plusCartPanelData(state,id){
			state.carPanelData.forEach((goods,index) =>{
				if(goods.sku_id === id){
					if(goods.count < goods.limit_num){
						goods.count ++
						return
					}
				}
			})
		},
		//减少
		subCartPanelData(state,id){
			state.carPanelData.forEach((goods,index) =>{
				if(goods.sku_id === id){
					if(goods.count > 1){
						goods.count --
						return
					}
				}
			})
		},
		//选择or取消选择商品
		checkGoods(state,id){
			state.carPanelData.forEach((goods,index) =>{
				if(goods.sku_id === id){
					goods.check = !goods.check
					return
				}
			})
		},
		//全选or全不选商品
		allCheckGoods(state,allChecked){
			state.carPanelData.forEach((goods,index) =>{
				goods.check = !allChecked
			})
			// this.allChecked = !this.allChecked
		},
		//删除选中的商品
		delCheckGoods(state){
// 			state.carPanelData.forEach((goods,index) =>{
// 				if(goods.check){
// 					state.carPanelData.splice(index,1)
// 				}
// 			})
			let i = state.carPanelData.length
			while(i--){
				if(state.carPanelData[i].check){
					state.carPanelData.splice(i,1)
				}
			}
		},
		//添加收货人信息
		submitInfo(state,data){
			if(data.default){
				state.receiveInfo.forEach((receive) =>{
					receive.default = false
				})
			}
			state.receiveInfo.push(data)
		},
		//修改收货人信息
		submitUpdateInfo(state,data){
			if(data.popInfo.default){
				state.receiveInfo.forEach((receive) =>{
					receive.default = false
				})
			}
			state.receiveInfo[data.popIndex] = data.popInfo
		},
		//提交订单
		submitOrder(state,data){
			state.orderData.unshift(data)
			let i=state.carPanelData.length
			while(i--){
				if(state.carPanelData[i].check){
					state.carPanelData.splice(i,1)
				}
			}
		},
		//支付成功
		payNow(state,id){
			state.orderData.forEach((order) => {
				if(order.orderId === id){
					order.isPay = true
					return
				}
			})
		},
		//更改默认收货地址
		updateDefault(state,index){
			state.receiveInfo.forEach((receive,i) =>{
				if(i==index){
					receive.default = true
				}else{
					receive.default = false
				}
			})
		},
		//删除收货地址
		deletePop(state,index){
			state.receiveInfo.splice(index,1)
		}
	}
})

export default store


