import Vue from 'vue'
import Router from 'vue-router'
import '../assets/css/header.css'
import '../assets/css/reset.css'

Vue.use(Router)

import Home from '../views/home'
import Shop from '../views/shop'
import Shop_One from '../views/shop_one'
import Shop_Two from '../views/shop_two'
import Shop_Three from '../views/shop_three'
import Shop_Fore from '../views/shop_fore'
import Shop_Five from '../views/shop_five'
import Shop_Six from '../views/shop_six'
import Item from '../views/item'
import Cart from '../views/cart'
import Checkout from '../views/checkout'
import Payment from '../views/payment'
import Account from '../views/account'
import Order from '../views/account/order'
import Address from '../views/account/address'

export default new Router({
	// mode: 'history',
  routes: [
		{
			path:'/',
			component:Home
		},
		{
			path:'/Home',
			component:Home
		},
		{
			path:'/Shop',
			component:Shop
		},
		{
			path:'/Shop_One',
			component:Shop_One
		},
		{
			path:'/Shop_Two',
			component:Shop_Two
		},
		{
			path:'/Shop_Three',
			component:Shop_Three
		},
		{
			path:'/Shop_Fore',
			component:Shop_Fore
		},
		{
			path:'/Shop_Five',
			component:Shop_Five
		},
		{
			path:'/Shop_Six',
			component:Shop_Six
		},
		{
			path:'/Item',
			name:'Item',
			component:Item
		},
		{
			path:'/Cart',
			name:'Cart',
			component:Cart
		},
		{
			path:'/Checkout',
			name:'Checkout',
			component:Checkout
		},
		{
			path:'/Payment',
			name:'Payment',
			component:Payment
		},
		{
			path:'/Account',
			component:Account,
			children:[{
				path:'',
				name:'Account',
				component:Order
			},
			{
				path:'/Address',
				name:'Address',
				component:Address
			}
			]
		}
  ]
})



